
import { Dialog,Box,Typography ,List,ListItem,styled} from '@mui/material'
import { qrCodeImage } from '../../consents/data' ;
import { useContext } from 'react';
import { Accountcontext } from '../../context/Acountprovider';
import { adduser } from '../../service/api';

import {GoogleLogin} from '@react-oauth/google'
import jwt_decode from "jwt-decode";
const dialogstyle={
    height:'96%',
    marginTop:'12%',
    width:'60%',
    maxWidth:'100%',
    maxHeight:'100%',
    boxShadow:'none',
    overflow:'none'
}
const Component=styled(Box)`
display:flex;

`
const Container=styled(Box)`
 padding:56px 0px 56px 56px;
`

const Qrcode=styled('img')({
    height:200,
    width:200,
    margin:'50px 0 0 50px'
})

const Titel=styled(Typography)`
font-size:26px;
font-weight:300;
color:#525252;
margin-bottom:25px
`

const LoginDialog = () => {
  
  const {setAccount}=useContext(Accountcontext);
  const onLoginSuccess = async(res) => {
    const decode = jwt_decode(res.credential);
    setAccount(decode);
    await adduser(decode);
    
  };
  
    const onLoginError=(res)=>{
        console.log("error" +res);
    }
  return (
    <Dialog 
    open={true} PaperProps={{sx:dialogstyle}} hideBackdrop={true}>
      <Component>
          <Container>
            <Titel>To use Whatsapp on computer:</Titel>
            <List>
                <ListItem>1.Open Whatsapp on computer</ListItem>
                <ListItem>2.Tap Menu Setting and select whatsapp web</ListItem>
                <ListItem>3.Point your phone to this screen to capture the code </ListItem>
            </List>
          </Container>
          <Box style={{position:'relative'}}>
             <Qrcode src={qrCodeImage} alt="qr code" />
             <Box style={{position:'absolute',top:'50%',transform:'translate(25%)'}}><GoogleLogin   onSuccess={onLoginSuccess} onError={onLoginError}/></Box>
          </Box>
      </Component>
    </Dialog>
  )
}

export default LoginDialog
