import { Box ,styled} from '@mui/material'
import Chatfooter from './Chatfooter'
import {useContext,useEffect,useState} from 'react';
import { Accountcontext } from '../../../context/Acountprovider';
import { getMessage, newMessage } from '../../../service/api';
import Message from './Message';


const Wrapper=styled(Box)`
 background-image:url(${'https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png'});
 background-height:50%;
`
const Component=styled(Box)`
height:80vh;
overflow-y:scroll;
`

const Container=styled(Box)`
padding:3px 18px;
`

const Messages = ({person,conversation}) => {
  const [value,setValue]=useState('');
  const [messages,setMessages]=useState([])
  const [file,setFile]=useState();
  const [incomingmessage,setIncomingmessage]=useState(null);

  const {account,socket,newmessagelag,setNewmessagelag}=useContext(Accountcontext);

  useEffect(()=>{
socket.current.on('getMessage',data=>{
    setIncomingmessage({
      ...data,
      createdAt:Date.now()
    })
})
  },[])

  useEffect(()=>{
   incomingmessage&& conversation ?.members?.includes(incomingmessage.senderId)&&
      setMessages(prev=>[...prev,incomingmessage])
  },[incomingmessage,conversation])

  useEffect(()=>{
      const getmessageDetails=async()=>{
       let data= await getMessage(conversation._id);
       setMessages(data);
      }
      conversation._id&&getmessageDetails();
  },[person._id,conversation._id,newmessagelag])

  const sentText=async(e)=>{
   const code=e.keyCode||e.which;
   if(code===13){
      let message={
        senderId:account.sub,
        reciverId:person.sub,
        conversationId:conversation._id,
        type:'text',
        value:value
      }


      socket.current.emit('sendMessage',message);
      await newMessage(message);

      setValue('');
      setNewmessagelag(prev=>!prev)
   }
  }
  return (
    <Wrapper>
        <Component>
           {
            messages&&messages.map((message)=>(
              <Container>
               <Message message={message}/>
              </Container>
              
            ))
           }
        </Component>
        <Chatfooter sentText={sentText} setValue={setValue} value={value} file={file} setFile={setFile}/>
    </Wrapper>
  )
}

export default Messages
