import { Box,styled,Divider } from '@mui/material';
import { getuser } from '../../../service/api';
import { useEffect,useState ,useContext} from 'react';
import Conversations from './Conversations';
import { Accountcontext } from '../../../context/Acountprovider';

const Components=styled(Box)`
height:80vh;
overflow:overlay;

`
const StyleDevider=styled(Divider)`

margin:0 0 0 70px;
background:#e9edef;
`

const Conversation = ({text}) => {

    const [users,setUsers]=useState([]);
    const {account,socket,setActiveuser}=useContext(Accountcontext);
    useEffect(()=>{
        const fetchData=async()=>{
         let res= await getuser();
         const filterData=res.filter(user=>user.name.toLowerCase().includes(text.toLowerCase()))
         setUsers(filterData);
        }
        fetchData();
    },[text]);

    useEffect(()=>{
      socket.current.emit('addUsers',account);
      socket.current.on("getUsers",users=>{
          setActiveuser(users);
      })
    },[account])
  return (
    <Components>
      {
        users.map((user)=>(
            user.sub!==account.sub && 
            <>
            <Conversations  user={user}/>
            <StyleDevider/>
            </>
        ))
      }
    </Components>
  )
}

export default Conversation
