import { Box, Typography } from '@mui/material'
import React from 'react'
import { emptyChatImage } from '../../../consents/data'
import styled from '@emotion/styled'
const Component=styled(Box)`
 background:#f8f9fa;
 padding:30px 0;
 text-align:center;
 height:100%;
 overflow:hidden;

`
const Container=styled(Box)`
padding:0 200px;

`
const Title=styled(Typography)`
  font-size:32px;
  margin:25px 0 10px 0;

`

const Subtitle=styled(Typography)`
font-weight:200;
font-size:12px;
color:#667781;

`

const Image=styled('img')({
  
  width:400,
  marginTop:184,
  
})


const Emptychat = () => {
  return (
    <Component>
       <Container>
          <Image src={emptyChatImage} alt="eimage" />
          <Title>Whatsapp Web</Title>
          <Subtitle>Now send or recive message without keeping your phone online.</Subtitle>
       </Container>
    </Component>
  )
}

export default Emptychat
