import React from 'react'
import { Box, Drawer, Typography, styled } from '@mui/material'
import {ArrowBack} from '@mui/icons-material';
import Profile from './Profile';

const drawerStyle={
  left:20,
  top:17,
  height:'95%',
  width:'30%',
  boxShadow:'none'

}
const Component=styled(Box)`
display:flex;
height:107px;
background:#008069;
color:#FFFFFF;
& > svg,&>p{
margin-top:auto;
padding:3px;
font-wight:600;

}

`

const Component1=styled(Box)`
background:#ededed;
`

const InfoDrawer = ({open,setOpen}) => {
  const handelClose=()=>{
    setOpen(false);
  }
  return (
    <Drawer
    open={open}
    onClose={handelClose}
    PaperProps={{sx:drawerStyle}}
    style={{zIndex:1500}}
    >
      <Component>
       <ArrowBack/>
       <Typography>Profile</Typography>
      </Component>
      <Component1>
        <Profile/>
      </Component1>
    </Drawer>
  )
}

export default InfoDrawer
