import { Box, Typography ,styled} from '@mui/material'
import { useContext } from 'react'
import { Search,MoreVert } from '@mui/icons-material'
import { defaultProfilePicture } from '../../../consents/data'
import { Accountcontext } from '../../../context/Acountprovider'

const Header=styled(Box)`

height:64px;
background:#ededed;
display:flex;
padding:8px 16px;
align-item:center;

`
const Image=styled('img')({
    width:50,
    height:50,
    borderRadius:'50%'
})
const Namecss=styled(Typography)`
margin-left:20px;


`

const Status=styled(Typography)`
margin-left:20px;
font-size:12px;
color:rgb(0,0,0,0.6);
`

const Icon=styled(Box)`
margin-left:auto;

`

const Chatheader = ({person}) => {

  const {activeuser}=useContext(Accountcontext);
  return (
    <Header>
      <Image src={person.picture||defaultProfilePicture} alt="dp" />
      <Box>
        <Namecss>{person.name}</Namecss>
        <Status>{activeuser?.find(user=>user.sub===person.sub)?'Online':'Ofline'}</Status>
      </Box>
      <Icon>
         <Search/>
         <MoreVert/>
      </Icon>
    </Header>
  )
}

export default Chatheader
