import styled from '@emotion/styled';
import { Box, Typography } from '@mui/material'
import {useContext, useEffect, useState} from 'react';
import { Accountcontext } from '../../../context/Acountprovider';
import { getConversation, setConversation } from '../../../service/api';
import { formatDate } from '../../utils/Commonutils';

const Compo=styled(Box)`
display:flex;
height:65px;
padding:13px 0;
cursor:pointer;
`

const Image=styled('img')({
    width:70,
    height:45,
    borderRadius:'50%',
    padding:'0 14px',
})




const Conversations = ({user}) => {

  const [message,setMessage]=useState({});
  const {setPerson,account,newmessagelag}=useContext(Accountcontext);
  const getusers=async()=>{
    setPerson(user);
    await setConversation({senderId:account.sub,reciverId:user.sub});
  }

  useEffect(()=>{
    const getconversationalDetails=async()=>{
      const data=await getConversation({senderId:account.sub,reciverId:user.sub});
      setMessage({text:data?.message,timestamp:data?.updateAt});
    }
    getconversationalDetails();
  },[newmessagelag])

  return (
    <Compo onClick={getusers}>
      <Box><Image src={user.picture} alt="dp" /></Box>
      <Box>
        <Typography>{user.name}</Typography>
        {
          message?.text && 
          <Typography>{formatDate(message?.timestamp)}</Typography>
        }
      </Box>
      <Box>
        {/* <Typography> {
          message?.text.includes('localhost')? 'media':message?.text}
          </Typography> */}
      </Box>
    </Compo>
  )
}

export default Conversations
